import React from 'react'
import {
    NavLink
} from 'react-router-dom'
import Button from 'components/button'
import { Input } from 'components/input'
import {
    Header,
} from './styles'
import {
    Title,
    Link
} from 'styles'


const LogIn = () => {

    return (
        <>
            <Header>
                <Title size='medium' align="center" padding="0px 20px">
                    Авторизация
                </Title>
            </Header>
            <Input
                autoFocus={true}
                required={true}
                // forwardRef={AuthName}
                autoComplete="off"
                label="Email"
                type="text"
                placeholder="example@gmail.com"
                padding="10px auto 5px auto"
                margin="0px 0px 10px 0"
                className="main"
            />
            <Input
                required={true}
                // forwardRef={AuthPassword}
                autoComplete="off"
                label="Пароль"
                type="password"
                placeholder="•••••••••"
                padding="10px auto 5px auto"
                margin="10px 0"
                className="main"
            />
            <Button
                kind='bold'
                margin='20px auto'
                className='main'
            >
                Войти
            </Button>
            <NavLink to="/auth/sign-in" activeClassName="" href="#">Еще нет в системе? Зарегистрируйтесь.</NavLink>
        </>
    )
}

export {
    LogIn
}

