import React from 'react'
import NotFound from 'components/not-found'
import { useStore } from 'effector-react'
import { $autoanswerStore, AutoanswerMenu, deleteAutoanswer, AutoanswerItem } from '.'

import {
    Flex,
    Grid,
    SubTitle,
    Text
} from 'styles'


const AutoanswerList = () => {

    const autoanswerList = useStore($autoanswerStore)


    return (
        <>
            <AutoanswerMenu />
            <Flex direction="column" align="flex-start">
                <Text className="gray" margin="5px 0px">Добавьте несколько автоответчиков, чтобы они срабатывали очередно для каждого пользователя</Text>
                <Text className="gray" margin="5px 0px 0px"  radius="var(--bradius) var(--bradius) 0 0px">Лист автоответчиков</Text>
            </Flex>
            <Grid gap="var(--spacing)" radius="0px var(--bradius) var(--bradius)">
                {
                    autoanswerList.map(i => {
                        return <AutoanswerItem key={i.id} data={i} onDelete={deleteAutoanswer} />
                    })
                }
                <NotFound title="Больше автоответчиков нет" caption="Добавьте новый автоответчик, чтобы он здесь отобразился" />
            </Grid>
        </>
    )
}

export {
    AutoanswerList
}


