import React, { useState } from 'react'
import {
    Container,
    Label,
    CheckboxWrapper,
    CheckboxLabel
} from './styles'

const Checkbox = (
    {
        id = '',
        label = '',
        onError = () => {},
        regExp,
        disable,
        caption = 'Неверные данные',
        width,
        padding = '20px 0px',
        margin = '10px 0px',
        className,
        ...args
    }) => {
    const [isChecked, setIsChecked] = useState(false)

    const onChange = (e) => {
        setIsChecked(() => {
            return !isChecked
        })
        return false
    }

    const clsName = isChecked ? className ? className : 'main' : 'gray'

    return (
        <CheckboxWrapper margin={margin} padding={padding} className={clsName}>
            <Container type="checkbox" id={id} onChange={onChange} {...args} className={className} />
            <CheckboxLabel htmlFor={id}></CheckboxLabel>
            <Label>{label}</Label>
        </CheckboxWrapper>
    )
}

export {
    Checkbox
}
