import React from 'react'
import { ShortcodeToolsItem } from '.'
import {
    Wrapper,
    Title,
    Grid,
} from 'styles'

const ShortcodeToolsBlock = () => {

    return (
        <Wrapper display="block" shadow="none" margin="0px" padding="0px">
            <Title margin="10px 5px 5px 5px">Классификаторы</Title>
            <Grid gap="30px">
                {
                    <>
                        <ShortcodeToolsItem />
                        <ShortcodeToolsItem />
                    </>
                }
            </Grid>
        </Wrapper>
    )
}

export {
    ShortcodeToolsBlock
}