import React from 'react'
import Button from 'components/button'
import { Select, Input } from 'components/input'
import {
    Flex,
    FormItem,
} from 'styles'

const ShortcodeToolsItem = () => {

    return (
        <FormItem>
            <Input
                id="shortcode_tools_1"
                width="100%"
                margin="10px 0px 0px 0px"
                padding="0px 0px 20px 0px"
                label="Классификатор"
                placeholder="__name__"
            />
            <Select
                id="shortcode_tools_1"
                width="100%"
                className="gray"
                margin="10px 0px 0px 0px"
                label="Значение классификатора"
                placeholder="Имя пользователя"
            />
            <Flex justify="space-between" margin="30px 0px 0px">
                <Button type="button" className="danger" kind="small">
                    Удалить
                </Button>
                <Button type="submit" className="main" kind="small">
                    Сохранить
                </Button>
            </Flex>
        </FormItem>
    )
}

export {
    ShortcodeToolsItem
}
