import styled from 'styled-components'

const types = {
    'small': {
        padding: '10px 15px',
        fontSize: '0.9em'
    },
    'medium': {
        padding: '10px 15px',
    },
    'bold': {
        padding: '12px 25px',
        fontSize: '16px !important'
    },
    'icon': {
        padding: '13px 20px',
        fontSize: '15px !important'
    }
}


const Container = styled.button`
    margin: ${props => props.margin ? props.margin : '0px'};
    pointer-events: ${props => props.loading === 'true' ? 'none' : 'auto'};
    width: ${props => props.width ? props.width : 'auto'};
    ${props => props.icon && `
        display: flex;
        align-items: center;
    `}
    /* opacity: ${props => props.loading === 'true' ? '.5' : '1'}; */
    ${props => props.loading === 'true' && `
        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 5px;
            background-color: inherit;
            z-index: -1;
        }
        & span {
            animation: fade-in .3s forwards ease-in-out;
        }
    `}
    ${props => props.kind && types[props.kind] ? {...types[props.kind]} : {...types['medium']}};
    padding: ${props => props.padding ?
    props.padding
    :
    props.kind && types[props.kind] ?
    types[props.kind].padding
    :
    types.medium.padding
    };
`



export {
    Container
}


