import React from 'react'
import { useStore } from 'effector-react'
import { ShortcodeToolsMenu, ShortcodeToolsBlock, $shortcodeToolsStore } from '.'

import {
    Flex
} from 'styles'


const ShortcodeTools = () => {

    const shortcodeToolsStore = useStore($shortcodeToolsStore)

    return (
        <Flex width="100%" direction="column">
            <ShortcodeToolsMenu />
            {
                shortcodeToolsStore.map(i => {
                    return (
                        <ShortcodeToolsBlock data={i} />
                    )
                })
            }
        </Flex>
    )
}

export {
    ShortcodeTools
}
