import React, { useState, Suspense } from 'react'
import { NavLink } from 'react-router-dom'
import { Input } from 'components/input'
import getIcon from 'components/icons'
import Button from 'components/button'
import {
    StickyMenu
} from 'styles'

const Modal = React.lazy(() => import('components/modal'))

const ShortcodeToolsMenu = () => {
    const [ isOpenAdd, setIsOpenAdd ] = useState(false)
    // const shortcodeList = useStore($shortcodeStore)

    const onChangeSearch = (e) => {
        // const foundItems = searchItems(shortcodeList, ['shortcode', 'message'], e.target.value)
        // changeShortcode(foundItems)
    }


    return (
        <>
            {isOpenAdd &&
                <Suspense fallback={""}>
                    <Modal onClose={setIsOpenAdd}>
                        {/* <ShortcodeNew onAdd={addShortcode} id={shortcodeList.length} onClose={setIsOpenAdd} /> */}
                    </Modal>
                </Suspense>
            }

            <StickyMenu padding="10px 5px">

                <Input
                    margin="0px auto 0px 0px"
                    padding="0px"
                    id="do_not_repeat"
                    type="search"
                    className="main"
                    placeholder="Поиск"
                    onChange={onChangeSearch}
                />

                <NavLink to="/settings/shortcodes" activeClassName="" className="link-icon gray">
                    <img src={getIcon('back')} alt="get back"/>
                </NavLink>

                <Button icon="add" kind="small" className="main link-icon" onClick={() => setIsOpenAdd(true)} />

            </StickyMenu>
        </>
    )
}

export {
    ShortcodeToolsMenu
}



