import { createEvent, createStore } from 'effector'

const addTrigger = createEvent()
const deleteTrigger = createEvent()

const $triggerStore = createStore([
    {
        id: 'trigger_1',
        keywords: "Добрый день\nПривет\nЗдравствуйте",
        trigger: 'Добрый день, чем я могу вам помочь?'
    },
    {
        id: 'trigger_2',
        keywords: 'Добрый день\nПривет\nЗдравствуйте',
        trigger: 'Добрый день, чем я могу вам помочь?'
    }
])
.on(addTrigger, (state, data) => [data, ...state])
.on(deleteTrigger, (state, id) => state.filter(i => i.id !== id))

export {
    $triggerStore,
    addTrigger,
    deleteTrigger
}
