import React from 'react'
import {
    Title,
} from '../styles'
import {
    Wrapper,
    Flex,
    Text
} from 'styles'

const DefaultSettings = () => {

    return (
        <>
            <Title>Настройки включают в себя ряд возможностей</Title>
            <Wrapper
                direction="column"
                shadow="none"
                padding="0px"
                align="flex-start"
                margin="none"
            >
                {/* <Checkbox
                    margin="5px"
                    id="autosave_checkbox"
                    label="Автоматически сохранять изменения в настройках"
                /> */}
                <Flex
                    direction="column"
                    align="flex-start"
                    margin="10px 0px"
                >
                    <Text className="main">Автоответчик</Text>
                    <Text className="gray" margin="15px 0px">Значимость этих проблем настолько очевидна, что реализация намеченных плановых заданий обеспечивает широкому кругу (специалистов) участие в формировании существенных финансовых и административных условий.</Text>
                </Flex>
                <Flex
                    direction="column"
                    align="flex-start"
                    margin="10px 0px"
                >
                    <Text className="main">Триггеры</Text>
                    <Text className="gray" margin="15px 0px">С другой стороны постоянный количественный рост и сфера нашей активности способствует подготовки и реализации форм развития. Товарищи! постоянный количественный рост и сфера нашей активности позволяет выполнять важные задания по разработке форм развития.</Text>
                </Flex>
                <Flex
                    direction="column"
                    align="flex-start"
                    margin="10px 0px"
                >
                    <Text className="main">Записи</Text>
                    <Text className="gray" margin="15px 0px">Значимость этих проблем настолько очевидна, что рамки и место обучения кадров влечет за собой процесс внедрения и модернизации направлений прогрессивного развития.</Text>
                </Flex>
                <Flex
                    direction="column"
                    align="flex-start"
                    margin="10px 0px"
                >
                    <Text className="main">Шорткоды</Text>
                    <Text className="gray" margin="15px 0px">Задача организации, в особенности же сложившаяся структура организации требуют от нас анализа существенных финансовых и административных условий. </Text>
                </Flex>
                <Flex
                    direction="column"
                    align="flex-start"
                    margin="10px 0px"
                >
                    <Text className="main">Статистика</Text>
                    <Text className="gray" margin="15px 0px">Идейные соображения высшего порядка, а также рамки и место обучения кадров представляет собой интересный эксперимент проверки систем массового участия.</Text>
                </Flex>
                <Flex
                    direction="column"
                    align="flex-start"
                    margin="10px 0px"
                >
                    <Text className="main">Черный список</Text>
                    <Text className="gray" margin="15px 0px">Таким образом постоянный количественный рост и сфера нашей активности позволяет выполнять важные задания по разработке существенных финансовых и административных условий.</Text>
                </Flex>
            </Wrapper>
        </>
    )
}

export {
    DefaultSettings
}

