import React from 'react'
import {
    NavWrapper
} from './styles'
import {
    NavLink
} from 'react-router-dom'

const NavLinks = () => {

    return (
        <NavWrapper>
            <NavLink to="/settings/auto-answer" className="link-button main">
                <span>Автоответчик</span>
                <i className="fas fa-calendar-check"></i>
            </NavLink>
            <NavLink to="/settings/triggers" className="link-button main">
                <span>Триггеры</span>
                <i className="fas fa-people-arrows"></i>
            </NavLink>
            <NavLink to="/settings/shortcodes" className="link-button main">
                <span>Шорткоды</span>
                <i className="fas fa-key"></i>
            </NavLink>
            <NavLink to="/settings/records" className="link-button main">
                <span>Записи</span>
                <i className="fas fa-microphone"></i>
            </NavLink>
            <NavLink to="/settings/reaction" className="link-button main">
                <span>Реакции</span>
                <i class="fas fa-bolt"></i>
            </NavLink>
            <NavLink to="/settings/stat" className="link-button main">
                <span>Статистика</span>
                <i className="fas fa-signal"></i>
            </NavLink>
            <NavLink to="/settings/chat" className="link-button main">
                <span>Группы</span>
                <i className="fas fa-users"></i>
            </NavLink>
            <NavLink to="/settings/sheduler" className="link-button main">
                <span>Планировщик</span>
                <i className="fas fa-clipboard-list"></i>
            </NavLink>
            <NavLink to="/settings/black-list" className="link-button main">
                <span>Черный список</span>
                <i className="fas fa-book"></i>
            </NavLink>
            <NavLink to="/settings" exact className="link-button main">
                <span>Подробнее</span>
                <i className="fas fa-info-circle"></i>
            </NavLink>
        </NavWrapper>
    )
}

export default NavLinks