import React, { useState } from 'react'
import { Textarea } from 'components/input'
import Button from 'components/button'
import {
    Title
} from '../styles'
import {
    FormItem,
    Flex,
} from 'styles'

const TriggerNew = ({ onAdd, id, onClose }) => {
    const [keywords, setKeywrods] = useState('')
    const [trigger, setTrigger] = useState('')

    const onSubmit = (e) => {
        e.preventDefault()
        if (!keywords.trim() || !trigger.trim()) {
            alert('Поле не заполнено!')
            return false
        }
        onClose(false)
        onAdd(
            {
                id: `trigger_${id+1}`,
                keywords: keywords.trim(),
                trigger: trigger.trim()
            }
        )
    }

    return (
        <FormItem onSubmit={onSubmit}>
            <Title margin="0px auto 15px auto">Новый триггер</Title>
            <Textarea
                padding="10px 0px 10px 0px"
                label="Ключевые слова клиента"
                placeholder="Добрый день&#x000D;Привет&#x000D;Здравствуйте"
                margin="10px 0px"
                className="gray"
                caption="Введите что-нибудь"
                value={keywords}
                onChange={(e) => setKeywrods(e.target.value)}
            />
            <Textarea
                padding="10px 0px 10px 0px"
                label="Ваш ответ"
                placeholder="Добрый день, чем я могу вам помочь?"
                margin="10px 0px"
                className="main"
                width="auto"
                value={trigger}
                onChange={(e) => setTrigger(e.target.value)}
            />
            <Flex justify="space-between" margin="10px 0px 0px">
                <Button type="button" className="gray" kind="small" onClick={() => onClose(false)}>
                    Отмена
                </Button>
                <Button className="main" kind="small">
                    Сохранить
                </Button>
            </Flex>
        </FormItem>
    )
}

export {
    TriggerNew
}
