import styled from 'styled-components'

const Container = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    animation: fade-in .5s forwards ease-in-out;
`

const Content = styled.div`
    padding: 10px;
    margin: 20px auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    color: #999999;
    opacity: .7;
    pointer-events: none;
    & span {
        display: block;
        margin: 10px 0px;
    }
    & img {
        margin-top: 15px;
        width: 150px;
        height: 150px;
    }
`

export {
    Container,
    Content
}