import { createEvent, createStore } from 'effector'

// shortcode store declaration:

const addShortcodeTools = createEvent()
const deleteShortcodeTools = createEvent()

const initialShortcodeToolsData = [
    {
        id: 'shortcode-tools-classifiers-data1',
        title: 'Классификаторы',
        data: [
            {
                id: 'shortcode-tools-classifiers-data2',
                shortcode: '%show_cost',
                message: 'Цена товара 2599$.'
            },
            {
                id: 'shortcode-tools-classifiers-data3',
                shortcode: '%show_links',
                message: 'Ссылки на наши источники находятся по адрессу ...'
            }
        ]
    },
    {
        id: 'shortcode-tools-identifiers-data1',
        title: 'Идентификаторы',
        data: [
            {
                id: 'shortcode-tools-identifiers-data2',
                shortcode: '%show_cost',
                message: 'Цена товара 2599$.'
            },
            {
                id: 'shortcode-tools-identifiers-data3',
                shortcode: '%show_links',
                message: 'Ссылки на наши источники находятся по адрессу ...'
            }
        ]
    }
]

const $shortcodeToolsStore = createStore(initialShortcodeToolsData)
.on(addShortcodeTools, (state, data) => [data, ...state])
.on(deleteShortcodeTools, (state, id) => state.filter(i => i.id !== id))


export {
    $shortcodeToolsStore,
    addShortcodeTools,
    deleteShortcodeTools,
}


