import React, { useState } from 'react'
import { Textarea } from 'components/input'
import Button from 'components/button'
import {
    FormItem,
    Flex,
} from 'styles'

const TriggerItem = ({ data, onDelete }) => {
    const [isDel, setisDel] = useState(false)

    const onSubmit = (e) => {
        e.preventDefault()
    }

    const onDeleteItem = () => {
        setTimeout(onDelete, 700, data.id)
        setisDel(true)
    }

    return (
        <FormItem onSubmit={onSubmit} className={isDel ? 'deleting' : ''}>
            <Textarea
                width="100%"
                padding="10px 0px 10px 0px"
                label="Ключевые слова клиента"
                placeholder="Введите ключевые слова пользователя, на которые должен отреагировать триггер"
                defaultValue={data.keywords || ''}
                margin="10px 0px"
                className="gray"
            />
            <Textarea
                padding="10px 0px 10px 0px"
                label="Ваш ответ"
                placeholder="Введите сообщение, которое будет отправлено в ответ на соообщение пользователя с ключевыми словами."
                defaultValue={data.trigger || ''}
                margin="10px 0px"
                className="main"
                width="100%"
            />
            <Flex justify="space-between" margin="10px 0px 0px">
                <Button type="button" className="gray" kind="small" onClick={onDeleteItem}>
                    Удалить
                </Button>
                <Button type="submit" className="main" kind="small">
                    Сохранить
                </Button>
            </Flex>
        </FormItem>
    )
}

export {
    TriggerItem
}
