import { createEvent, createStore } from 'effector'


const changeShortcode = createEvent()
const addShortcode = createEvent()
const deleteShortcode = createEvent()

const initialShortcodeStore = []

const $shortcodeStore = createStore(initialShortcodeStore)
.on(changeShortcode, (_, data) => data)
.on(addShortcode, (state, data) => [data, ...state])
.on(deleteShortcode, (state, id) => state.filter(i => i.id !== id))



const addClassifier = createEvent()
const deleteClassifier = createEvent()

const initialClassifierStore = [
    {
        id: 'classifier_1',
        shortcode: '%show_cost',
        message: 'Цена товара 2599$.'
    },
    {
        id: 'classifier_2',
        shortcode: '%show_links',
        message: 'Ссылки на наши источники находятся по адрессу ...'
    }
]

const $сlassifierStore = createStore(initialClassifierStore)
.on(addClassifier, (state, data) => [data, ...state])
.on(deleteClassifier, (state, id) => state.filter(i => i.id !== id))


export {
    $shortcodeStore,
    changeShortcode,
    addShortcode,
    deleteShortcode,
    $сlassifierStore,
    addClassifier,
    deleteClassifier
}
