import React from 'react'
import Faq from 'components/faq'

import {
    Title
} from '../styles'

import {
    FaqContent
} from 'styles'

const TriggerFaq = () => {

    return (
        <Faq>
            <FaqContent>
                <Title margin="0px 0px 20px 0px">Триггер</Title>
                Триггер - заготовленный текст для определенного сообщения. <br />
                Чтобы отредактировать текст в триггере, нажмите на текст и введите новое. <br />
                Перечисление ключевых слов пишите через перенос строки. <br />
                Исключите повторение триггера в беседе, чтобы пользователь не получал одинаковые ответы.<br />
            </FaqContent>
        </Faq>
    )
}

export {
    TriggerFaq
}
