import React from 'react'
import personIcon from 'assets/img/person.png'
import {
    Container,
    Header,
    ProfileIcon,
    Status,
    Title,
    ChatContent
} from './styles'

import {
    Wrapper
} from 'styles'

const Dialog = () => {

    return (
        <Container>
            <Header className="gray">
                <ProfileIcon>
                    <img loading="lazy" src={personIcon} alt="person" />
                </ProfileIcon>
                <Wrapper shadow="none" padding="0px" margin="0px 20px" direction="column" justify="space-between">
                    <Title>Thomas Edison</Title>
                    <Status>печатает...</Status>
                </Wrapper>
            </Header>
            <ChatContent>
                Content
            </ChatContent>
        </Container>
    )
}

export default Dialog