import React from 'react'
import { StatBar } from '.'

import {
    Wrapper,
    Divider
} from 'styles'

import {
    Title,
    Caption
} from '../styles'

const StatPage = () => {

    return (
        <>
            <Title>Статистика</Title>
            <Wrapper shadow="none" padding="0px" margin="20px 0px 40px 0px">
                <Caption>Сообщений в день</Caption>
                <StatBar />
            </Wrapper>
            <Divider className="main" />
            <Wrapper shadow="none" padding="0px" margin="20px 0px 40px 0px">
                <Caption>Новых пользователей в день</Caption>
                <StatBar label="Пользователей" colors={['rgba(0,0,0, .5)']} />
            </Wrapper>
        </>
    )
}

export {
    StatPage
}

