import styled from 'styled-components'

const Container = styled.div`
    position: fixed;
    bottom: var(--spacing);
    right: var(--spacing);
    margin-left: var(--spacing);
    display: block;
    border-radius: var(--bradius);
    transition: .4s;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    z-index: 1001;
    background-color: var(--gray-bcolor);
    padding: 5px;
    max-height: calc(100% - 40px);
    overflow: auto;
    transition: .3s;
    &:empty {
        opacity: 0;
        transition: .3s;
        padding: 0px;
    }
`

const PromptItem = styled.div`
    margin: 5px;
    display: flex;
    align-items: center;
    border-radius: var(--bradius);
    position: relative;
    background-color: var(--danger-bcolor);
    color: var(--danger-tcolor);
    padding: 15px 20px;
    font-size: 15px;
    letter-spacing: 0.02em;
    animation: scale-up .5s forwards ease-in-out;
    overflow: hidden;
    &:after {
        content: '';
        position: absolute;
        bottom: 0px;
        left: 0px;
        height: 100%;
        animation: deadline 10s 5s forwards linear;
        background-color: rgba(0,0,0, .1);
        border-radius: var(--bradius);
        z-index: -1;
    }
`

const ClosePrompt = styled.button`
    color: var(--danger-tcolor);
    font-size: 13px;
    padding: 5px 0px 5px 20px;
    cursor: pointer;
    min-width: 30px;
`

export {
    Container,
    PromptItem,
    ClosePrompt
}