import React, { useState, Suspense } from 'react'
import searchItems from 'components/search'
import { Input } from 'components/input'
import { useStore } from 'effector-react'
import Button from 'components/button'
import { ShortcodeNew, addShortcode, $shortcodeStore, changeShortcode, ShortcodeTools } from '.'
import {
    StickyMenu,
    Flex
} from 'styles'

const Modal = React.lazy(() => import('components/modal'))

const ShortcodeMenu = () => {
    const [ openModal, setOpenModal ] = useState('')
    const shortcodeList = useStore($shortcodeStore)

    const onChangeSearch = (e) => {
        const foundItems = searchItems(shortcodeList, ['shortcode', 'message'], e.target.value)
        changeShortcode(foundItems)
    }


    return (
        <>
            {openModal === 'add' ?
                <Suspense fallback={""}>
                    <Modal onClose={() => setOpenModal('')}>
                        <ShortcodeNew onAdd={addShortcode} id={shortcodeList.length} onClose={() => setOpenModal('')} />
                    </Modal>
                </Suspense>
                : openModal === 'tools' &&
                    <Suspense fallback={""}>
                        <Modal onClose={() => setOpenModal('')} width="100%">
                            <ShortcodeTools />
                        </Modal>
                    </Suspense>
            }

            <StickyMenu padding="10px 5px 10px 0px">

                    <Input
                        width="300px"
                        margin="5px auto 5px 0px"
                        id="do_not_repeat"
                        type="search"
                        className="main"
                        placeholder="Поиск шорткодов"
                        onChange={onChangeSearch}
                    />

                    <Flex margin="5px 0px">
                        <Button className="main link-icon" onClick={() => setOpenModal('tools')}>
                            <i className="fas fa-tools"></i>
                        </Button>

                        <Button className="main link-icon" onClick={() => setOpenModal('add')}>
                            <i className="fas fa-plus-circle"></i>
                        </Button>
                    </Flex>

            </StickyMenu>
        </>
    )
}

export {
    ShortcodeMenu
}


