import React, { useState } from 'react'
import {
    Wrapper,
    Container,
    Label,
    Error
} from './styles'

const Input = (
    {
        label = '',
        onError = () => {},
        regExp,
        disable,
        caption = 'Неверные данные',
        width,
        margin = '20px 0px',
        className,
        forwardRef,
        ...args
    }) => {
    const [isError, setError] = useState(false)

    const clsName = isError ? 'danger' : className ? className : 'main'

    const onChange = (e) => {
        if (!regExp) return false
        const value = e.target.value

        setError(!regExp.test(value) || !value)
        onError(!regExp.test(value) || !value)
    }

    return (
        <Wrapper width={width} margin={margin} disable={disable}>
            <Label>{label}</Label>
            <Container ref={forwardRef} onChange={onChange} {...args} className={clsName} />
            {isError &&
                <Error>{caption}</Error>
            }
        </Wrapper>
    )
}

export {
    Input
}
