import React from 'react'
import {
    NavLink
} from 'react-router-dom'
import Button from 'components/button'
import { Input } from 'components/input'
import {
    Header,
} from './styles'
import {
    Title,
    Link
} from 'styles'


const SignIn = () => {

    return (
        <>
            <Header>
                <Title size='medium' align="center" padding="0px 20px">
                    Регистрация
                </Title>
            </Header>
                <Input
                    autoFocus={true}
                    required={true}
                    // forwardRef={AuthName}
                    autoComplete="off"
                    label="Имя"
                    type="text"
                    placeholder="Samuel"
                    padding="10px auto 5px auto"
                    margin="0px 0px 10px 0px"
                    className="main"
                />
                <Input
                    autoFocus={false}
                    required={true}
                    // forwardRef={AuthName}
                    autoComplete="off"
                    label="Email"
                    type="text"
                    placeholder="example@gmail.com"
                    padding="10px auto 5px auto"
                    margin="10px 0"
                    className="main"
                />
                <Input
                    required={true}
                    // forwardRef={AuthPassword}
                    autoComplete="off"
                    label="Пароль (5 - 20 символов)"
                    type="password"
                    placeholder="•••••••••"
                    padding="10px auto 5px auto"
                    margin="10px 0"
                    className="main"
                />
                <Button
                    kind='bold'
                    margin='20px auto'
                    className='main'
                >
                    Зарегистрироваться
                </Button>
                <NavLink to="/auth/login" activeClassName="" href="#">Уже есть в системе? Войдите.</NavLink>
        </>
    )
}

export {
    SignIn
}


