import React from 'react'
import Button from 'components/button'
import {
    Flex
} from 'styles'


const ShortcodeItemMenu = ({ changed = false }) => {

    return (
        <Flex margin="0px">
            <Button type="button" className={`link-icon ${changed ? 'main' : 'gray'}`} kind="small">
                <i className="fas fa-save"></i>
            </Button>
            <Button type="button" className="link-icon danger">
                <i className="fas fa-trash-alt"></i>
            </Button>
        </Flex>
    )
}

export {
    ShortcodeItemMenu
}



