import React, { useState } from 'react'
import Modal from 'components/modal'

import {
    FAQ
} from 'styles'

const Faq = ({ children }) => {
    const [isOpen, setIsOpen] = useState(false)

    return (
        <FAQ onClick={() => setIsOpen(!isOpen)} className="main">
            <i class="fas fa-question"></i>
            {isOpen &&
                <Modal>
                    {children}
                </Modal>
            }
        </FAQ>
    )
}

export default Faq

