import React from 'react'
import { Line } from 'react-chartjs-2'

const i = {
    label: 'Сообщений',
    color: ['rgba(18,70,245)'],
    labels: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
    data: [699, 444, 322, 213, 334, 434, 644, 789, 555, 277, 455, 434]
}

const StatBar = ({ label = i.label, colors = i.color, stat: { labels = i.labels, numbers = i.data } = {}}) => {

    const data = {
        labels: labels,
        datasets: [
            {
                label: label,
                data: numbers,
                borderRadius: 5,
                backgroundColor: colors,
                borderColor: colors,
                borderWidth: 1,
            }
        ],
    }

    return (
        <Line data={data} />
    )
}

export {
    StatBar
}

