import image from 'assets/img/image.svg'
import chatIcon from 'assets/img/chat.svg'
import settingsIcon from 'assets/img/settings.svg'
import userIcon from 'assets/img/user.svg'
import add from 'assets/img/add.svg'
import autoAnswer from 'assets/img/auto-answer.svg'
import trigger from 'assets/img/trigger.svg'
import records from 'assets/img/records.svg'
import shortcodes from 'assets/img/shortcodes.svg'
import stat from 'assets/img/stat.svg'
import tools from 'assets/img/tools.svg'
import group from 'assets/img/group.svg'
import rightArrow from 'assets/img/right-arrow.svg'
import blackList from 'assets/img/black-list.svg'
import faq from 'assets/img/faq.svg'
import checked from 'assets/img/checked.svg'
import logo from 'assets/img/logo.svg'
import sheduler from 'assets/img/sheduler.svg'
import search from 'assets/img/search.svg'
import back from 'assets/img/previous.svg'
import trash from 'assets/img/trash.svg'
import save from 'assets/img/save.svg'
import notFound from 'assets/img/not-found.svg'

const icons = {
    'image': image,
    'chat': chatIcon,
    'search': search,
    'back': back,
    'save': save,
    'trash': trash,
    'checked': checked,
    'settings': settingsIcon,
    'user': userIcon,
    'add': add,
    'tools': tools,
    'sheduler': sheduler,
    'autoAnswer': autoAnswer,
    'trigger': trigger,
    'records': records,
    'shortcodes': shortcodes,
    'stat': stat,
    'group': group,
    'right-arrow': rightArrow,
    'blackList': blackList,
    'faq': faq,
    'logo': logo,
    'not-found': notFound
}

const getIcon = (kind) => {
    return icons[kind] || icons['image']
}

export default getIcon
