import { createEvent, createStore } from 'effector'

const spinerOn = createEvent()
const spinerOff = createEvent()

const initialPrompts = {
    is: false
}

const $spinnerStore = createStore(initialPrompts)
.on(spinerOff, state => {
    return {is: false}
})
.on(spinerOn, state => {
    return {is: true}
})

export {
    $spinnerStore,
    spinerOn,
    spinerOff
}


