import React from 'react'
import { Input, Textarea } from 'components/input'
import { AutoanswerItemMenu } from '.'
import {
    FormItem,
    Flex,
    Text
} from 'styles'

const AutoanswerItem = ({ ...args }) => {

    return (

        <FormItem {...args}>
            <AutoanswerItemMenu />
            <Flex margin="0px 5px" direction="column">
                <Input
                    width="100%"
                    padding="10px 0px"
                    label="Шорткод"
                    placeholder="Введите шорткод"
                    margin="10px 0px 0px 0px"
                    className="main"
                    // forwardRef={shortcode}
                    // defaultValue={data.shortcode || ''}
                />
                <Textarea
                    padding="10px 0px"
                    label="Сообщение"
                    placeholder="Введите текст сообщения"
                    margin="10px 0px 0px"
                    className="main"
                    width="100%"
                    // forwardRef={message}
                    // defaultValue={data.message || ''}
                />
            </Flex>
            <Flex margin="5px 5px 0px 5px" justify="space-between">
                <Text padding="0px" size="13px" opacity=".5">3 days ago</Text>
                <Text padding="0px" size="13px" opacity=".5">17:35 &nbsp;•&nbsp; 25 Aprel &nbsp; <i class="fas fa-pen" style={{fontSize: '11px'}}></i></Text>
            </Flex>
        </FormItem>
    )
}

export {
    AutoanswerItem
}


