const searchItems = ( arrItems = [], arrKeys = [], searchString = '' ) => {
    
    const result = arrItems.filter(i => {

        const isFetch = arrKeys.find(str => {

            if (Object.keys(i).includes(str) && i[str].indexOf(searchString) + 1) {

                return true

            }

            return false
        })

        return isFetch
    })

    return result

}

export default searchItems

