import styled from 'styled-components'

const fontSize = {
    'bold': '32px',
    'medium': '24px',
    'regular': '18px',
    'small': '14px'
}

const Wrapper = styled.div`
    margin: ${props => props.margin ? props.margin : '0px auto'};
    padding: ${props => props.padding ? props.padding : '20px'};
    border-radius: ${props => props.radius ? props.radius : '7px'};
    box-shadow: ${props => props.shadow ? props.shadow : '0px 0px 5px 1px rgb(0 0 0 / 10%)'};
    max-width: ${props => props.maxWidth ? props.maxWidth : '100%'};
    min-width: ${props => props.minWidth ? props.minWidth : 'auto'};
    display: ${props => props.display ? props.display : 'flex'};
    width: ${props => props.width ? props.width : 'auto'};
    z-index: ${props => props.zIndex ? props.zIndex : 'inherit'};
    flex-wrap: ${props => props.wrap ? props.wrap : 'wrap'};
    background-color: ${props => props.bg ? props.bg : 'inherit'};
    align-items: ${props => props.align ? props.align : 'strecth'};
    flex-direction: ${props => props.direction ? props.direction : 'row'};
    justify-content: ${props => props.justify ? props.justify : 'none'};
    animation: ${props => props.animation ? props.animation : 'none'};
    transition: .3s;
`

const Title = styled.h3`
    display: block;
    font-size: ${props => props.size ? fontSize[props.size] ? fontSize[props.size] : props.size : '16px'};
    text-align: ${props => props.align ? props.align : 'left'};
    text-transform: ${props => props.transform ? props.transform : 'none'};
    margin: ${props => props.margin ? props.margin : '0px'};
    padding: ${props => props.padding ? props.padding : '0px'};
    color: ${props => props.color ? props.color : '#000'};
    font-weight: ${props => props.weight ? props.weight : 'normal'};
    letter-spacing: 0.02em;
`

const SubTitle = styled.span`
    display: block;
    font-size: ${props => props.size ? fontSize[props.size] ? fontSize[props.size] : props.size : '17px'};
    text-align: ${props => props.align ? props.align : 'left'};
    text-transform: ${props => props.transform ? props.transform : 'none'};
    margin: ${props => props.margin ? props.margin : '0px'};
    padding: ${props => props.padding ? props.padding : '0px'};
    color: ${props => props.color ? props.color : '#000'};
    font-weight: ${props => props.weight ? props.weight : 'normal'};
    letter-spacing: 0.02em;
`

const Link = styled.a`
    font-size: ${props => props.size ? fontSize[props.size] ? fontSize[props.size] : props.size : '16px'};
    text-transform: ${props => props.transform ? props.transform : 'none'};
    margin: ${props => props.margin ? props.margin : '0px'};
    padding: ${props => props.padding ? props.padding : '0px'};
    color: var(--link-color);
    font-size: 14px;
    font-weight: ${props => props.weight ? props.weight : 'normal'};
    letter-spacing: 0.02em;
`

const Flex = styled.div`
    width: ${props => props.width ? props.width : 'auto'};
    margin: ${props => props.margin ? props.margin : '0px auto'};
    display: ${props => props.display ? props.display : 'flex'};
    flex-wrap: ${props => props.wrap ? props.wrap : 'wrap'};
    align-items: ${props => props.align ? props.align : 'stretch'};
    justify-content: ${props => props.justify ? props.justify : 'none'};
    flex-direction: ${props => props.direction ? props.direction : 'row'};
    border-radius: var(--bradius);
`

const Grid = styled.div`
    width: ${props => props.width ? props.width : 'auto'};
    display: grid;
    grid-template-columns: ${props => props.minmax ? 'repeat(auto-fill,minmax('+props.minmax+',1fr))' : 'repeat(auto-fill,minmax(280px,1fr))'};
    grid-gap: ${props => props.gap ? props.gap : '0px'};
    border-radius: ${props => props.radius ? props.radius : 'var(--bradius)'};
    padding: ${props => props.padding ? props.padding : 'var(--spacing)'};
    background-color: var(--gray-bcolor);
    @media (max-width: 500px) {
        grid-template-columns: repeat(auto-fill,minmax(200px,1fr));
    }
`

const ScrollOptions = styled.div`
    max-height: 100%;
    padding: ${props => props.padding ? props.padding : '10px'};
    margin: ${props => props.margin ? props.margin : '0px'};
    overflow: auto;
    display: flex;
    flex-direction: column;
    border-radius: var(--radius);
    box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.07);
`

const FormItem = styled.form`
    display: flex;
    flex-direction: column;
    margin: ${props => props.margin ? props.margin : '0px'};
    padding: 15px;
    border-radius: var(--bradius);
    background-color: #fff;
    box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.10);
    will-change: transform;
    width: 100%;
    position: relative;
    min-width: 250px;
    animation: item .3s forwards ease-in-out;
    &.deleting {
        pointer-events: none;
        transition: 1s;
        animation: deleting .7s forwards ease-in-out;
    }
`

const FAQ = styled.button`
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    max-width: 300px;
    padding: 5px 7px;
    & i {
        font-size: 20px;
        color: var(--main-bcolor);
    }
`

const FaqContent = styled.div`
    max-width: 500px;
`

const BreadcrumbsList = styled.div`
    margin: ${props => props.margin || '0px'};
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    & a {
        font-size: 15px;
        padding-right: 15px;
        opacity: .5;
        position: relative;
    }
    & a:after {
        content: '/';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 3px;
        opacity: .5;
    }
    & a:last-child:after {
        display: none;
    }
    & a:hover {
        opacity: 1;
    }
`

const Divider = styled.div`
    display: block;
    border-radius: 50%;
    margin: ${props => props.margin || '0px'};
    opacity: ${props => props.opacity || '1'};
    height: 2px;
    border: 1px solid;
`

const Sticky = styled.div`
    position: sticky;
    display: block;
    top: ${props => props.top || 'auto'};
    bottom: ${props => props.bottom || 'auto'};
    z-index: 100;
    background-color: #fff;
`

const Fixed = styled.div`
    position: fixed;
    top: ${props => props.top || 'auto'};
    left: ${props => props.left || 'auto'};
    right: ${props => props.right || 'auto'};
    bottom: ${props => props.bottom || 'auto'};
    z-index: 100;
    border: 5px solid #fff;
    background-color: #fff;
    border-radius: var(--bradius);
    overflow: hidden;
`

const StickyMenu = styled.div`
    position: sticky;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: ${props => props.padding ? props.padding : '0px'};
    margin: ${props => props.margin ? props.margin : '0px'};
    z-index: 100;
    background-color: #fff;
`

const Text = styled.span`
    display: inline-block;
    margin: ${props => props.margin ? props.margin : '0px'};
    padding: ${props => props.padding ? props.padding : '10px 15px'};
    transition: .3s;
    border-radius: ${props => props.radius ? props.radius : 'var(--bradius)'};
    text-decoration: none;
    font-size: ${props => props.size ? props.size : '15px'};
    line-height: 1.5em;
    letter-spacing: 0.02em;
    opacity: ${props => props.opacity ? props.opacity : '1'};
`

const Image = styled.div`
    width: ${props => props.width ? props.width : '100%'};
    height: ${props => props.height ? props.height : '100%'};
    margin: ${props => props.margin ? props.margin : '0px'};
    padding: ${props => props.width ? props.width : '0px'};
`


export {
    Wrapper,
    Title,
    SubTitle,
    Link,
    Flex,
    Grid,
    ScrollOptions,
    FormItem,
    FAQ,
    FaqContent,
    BreadcrumbsList,
    Divider,
    Sticky,
    Fixed,
    StickyMenu,
    Text,
    Image,
}


