import React from 'react'
import NotFound from 'components/not-found'
import { useStore } from 'effector-react'
import { ShortcodeItem, ShortcodeMenu, $shortcodeStore, deleteShortcode } from '.'

import {
    Grid,
    Text
} from 'styles'


const ShortcodeList = () => {

    const shortcodeList = useStore($shortcodeStore)

    return (
        <>
            <ShortcodeMenu />
            <Text className="gray" radius="var(--bradius) var(--bradius) 0 0px">Лист шорткодов</Text>
            <Grid gap="var(--spacing)" radius="0px var(--bradius) var(--bradius)">
                {
                    shortcodeList.map(i => {
                        return <ShortcodeItem key={i.id} data={i} onDelete={deleteShortcode} />
                    })
                }
                <NotFound title="Больше шорткодов нет" caption="Добавьте новый шорткод, чтобы он здесь отобразился" />
            </Grid>
        </>
    )
}

export {
    ShortcodeList
}
