import React, { useState } from 'react'
import Modal from 'components/modal'
import {
    Wrapper,
    ContainerSelect,
    SelectOptions,
    Label,
} from './styles'

const Select = (
    {
        label = '',
        onError = () => {},
        regExp,
        disable,
        caption = 'Неверные данные',
        width,
        padding = '20px 0px',
        className = 'main',
        ...args
    }) => {

    const [selectValue] = useState('Значение')
    const [isOpen, setIsOpen] = useState(false)

    return (
        <Wrapper onClick={() => setIsOpen(!isOpen)} width={width} padding={padding} disable={disable}>
            <Label>{label}</Label>
            <ContainerSelect type="text" {...args} className={className} value={selectValue} readOnly />
            {
                isOpen &&
                <Modal padding="0px">
                    <SelectOptions className={className}>
                        <button className={className}>ник пользователя</button>
                        <button className={className}>имя пользователя</button>
                        <button className={className}>id пользователя</button>
                        <button className={className}>ссылка на пользователя</button>
                    </SelectOptions>
                </Modal>
            }
        </Wrapper>
    )
}

export {
    Select
}
