import React from 'react'
import {
    Container,
} from './styles'

const Button = (
        {
            className = '',
            disable,
            isLoading = false,
            icon,
            kind,
            children = false,
            onClick = () => {},
            ...args
        }
    ) => {

    const onClicked = (e) => {
        onClick(e)
    }

    return (
        <Container
            tabIndex={disable ? '-1' : 'auto'}
            loading={isLoading ? 'true' : 'false'}
            icon={!!icon}
            content={children ? "true" : undefined}
            kind={kind}
            className={disable ? className + ' disable' : className}
            onClick={onClicked}
            {...args}
        >
            {icon &&
                <i className={icon}></i>
            }
            {children &&
                <span>
                    {children}
                </span>
            }
        </Container>
    )
}

export default Button
