import styled from 'styled-components'


const Wrapper = styled.div`
    margin: ${props => props.margin ? props.margin : '20px 0px'};
    padding: ${props => props.outerPadding ? props.outerPadding : '0px'};
    display: inline-flex;
    align-items: flex-start;
    flex-direction: column;
    cursor: ${props => props.disable ? 'not-allowed' : 'default'};
    width: ${props => props.width ? props.width : '300px'};
    position: relative;
    @media (max-width: 500px) {
        width: 100%;
    }
    max-width: 100%;
    ${props => props.disable && `
        cursor: not-allowed;
        opacity: .7;
        & * {
            pointer-events: none;
        }
    `};
`

const Label = styled.span`
    font-size: 15px;
`

const ContainerTextarea = styled.textarea`
    margin: ${props => props.margin ? props.margin : '0px'};
`

const Container = styled.input`
    margin: ${props => props.padding ? props.padding : '0px'};
`

const Error = styled.span` 
    color: #C25A6C;
    font-size: .8em;
    animation: fade-in .5s forwards ease-out;
    text-align: left;
`

const CheckboxWrapper = styled.div`
    margin: ${props => props.margin ? props.margin : '10px 0px'};
    padding: 10px 15px;
    border-radius: var(--bradius);
    display: inline-flex;
    align-items: flex-start;
    position: relative;
    transition: .3s;
    & input {
        transition: .2s;
        color: currentColor !important;
    }
    & span {
        padding-left: 20px;
        margin-bottom: 0px;
        line-height: 21px;
    }
`

const CheckboxLabel = styled.label`
    opacity: 0;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
`

const ContainerSelect = styled.input`
    cursor: pointer;
    margin: ${props => props.margin ? props.margin : '0px'};
    -webkit-tap-highlight-color: transparent;
    user-select: none;
`

const SelectOptions = styled.div`
    animation: fade-in .2s forwards ease-in-out;
    border: 2px solid;
    border-top: none;
    border-radius: 0px 0px var(--bradius) var(--bradius);
    font-size: .9em;
    z-index: 10;
    cursor: pointer;
    overflow-y: auto;
    max-height: 200px;
    box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.03);
    & button {
        display: block;
        width: 100%;
        text-align: left;
        border: none;
        padding: 10px 20px;
        border-radius: 0px;
        border-bottom: 1px solid currentColor;
    }
`

export {
    Wrapper,
    Container,
    ContainerTextarea,
    Label,
    Error,
    CheckboxWrapper,
    CheckboxLabel,
    ContainerSelect,
    SelectOptions
}
