import React, { useState } from 'react'
import { useStore } from 'effector-react'
import Breadcrumbs from 'components/breadcrumbs'
import NotFound from 'components/not-found'
import Button from 'components/button'
import { Checkbox } from 'components/input'
import Modal from 'components/modal'
import { TriggerItem, TriggerNew, TriggerFaq, $triggerStore, addTrigger, deleteTrigger } from '.'
import {
    Title,
} from '../styles'
import {
    Wrapper,
    Flex,
    Grid
} from 'styles'

const TriggerPage = () => {
    const [ isOpen, setIsOpen ] = useState(false)

    const triggerList = useStore($triggerStore)

    return (
        <>
            {isOpen &&
                <Modal onClose={setIsOpen} fullHeight={true}>
                    <TriggerNew onAdd={addTrigger} id={triggerList.length} onClose={setIsOpen} />
                </Modal>
            }
            <Flex justify='space-between' margin="0px 0px 5px 0px">
                <Title margin="10px 10px 10px 0px">Триггеры</Title>
                <TriggerFaq />
            </Flex>
            <Wrapper
                margin="0px"
                padding="10px 0px"
                justify="space-between"
                align="center"
                shadow="none"
            >
                <Checkbox
                    id="do_not_repeat"
                    className="main"
                    label="Исключить повторения триггера в беседе"
                />
                <Button margin="10px 0px" icon="fas fa-plus-circle" kind="small" className="main" onClick={() => setIsOpen(true)}>
                    Добавить триггер
                </Button>
            </Wrapper>
                {
                    triggerList.length > 0 ?
                        <Grid gap="20px">
                            {
                                triggerList.map(i => {
                                    return <TriggerItem key={i.id} data={i} onDelete={deleteTrigger} />
                                })
                            }
                        </Grid>
                        :
                        <NotFound title="Триггеров не найдено" caption="Добавьте триггер, чтобы он здесь отобразился" />
                }
        </>
    )
}

export {
    TriggerPage
}
