import React, { useState, useEffect } from 'react'
import ReactDOM from 'react-dom'
import { useStore } from 'effector-react'
import {
    $promptStore,
    deletePrompt
} from 'utils/prompt'
import {
    Container,
    PromptItem,
    ClosePrompt
} from './styles'
import {
    Flex
} from 'styles'

const Prompt = ({ className, label }) => {
    const [toShow, setToShow] = useState(true)

    const promptElement = document.getElementById('bag')
    const promptList = useStore($promptStore)

    // useEffect(() => {
    //     const timed = setTimeout(setToShow, 5000, false)
    //     return () => {
    //         clearTimeout(timed)
    //     }
    // }, [])

    if (!toShow) return false
    return (
        ReactDOM.createPortal(
            <Container className={className}>
                {
                    promptList.map(({ id, message }) => {
                        return (
                            <PromptItem key={id} align="center" margin="5px">
                                <span><i class="fas fa-exclamation-triangle"></i> &nbsp; {message}</span>
                                <ClosePrompt onClick={() => deletePrompt(id)} className="under"><i class="fas fa-times"></i></ClosePrompt>
                            </PromptItem>
                        )
                    })
                }
            </Container>,
            promptElement
        )
    )
}

export default Prompt
