import React from 'react'
import getIcons from 'components/icons'
import {
    Container,
    Content
} from './styles'

const NotFound = ({ title = 'Ничего не найдено', caption = '' }) => {

    return (
        <Container>
            <Content>
                <h2>{title}</h2>
                <img src={getIcons('not-found')} alt="not found" />
                {caption &&
                    <span>{caption}</span>
                }
            </Content>
        </Container>
    )
}

export default NotFound