import { createEvent, createStore } from 'effector'


const addAutoanswer = createEvent()
const deleteAutoanswer = createEvent()

const initialAutoanswerStore = [
    {
        id: 'autoanswer_1',
        shortcode: '%show_cost',
        message: 'Цена товара 2599$.'
    }
]

const $autoanswerStore = createStore(initialAutoanswerStore)
.on(addAutoanswer, (state, data) => [data, ...state])
.on(deleteAutoanswer, (state, id) => state.filter(i => i.id !== id))


export {
    $autoanswerStore,
    addAutoanswer,
    deleteAutoanswer
}
