import React from 'react'
import ReactDOM from 'react-dom'
import {
    Container,
    Content
} from './styles'

const Modal = ({ children = 'Content is empty', padding, onClose = () => {}, width, fullHeight }) => {

    const promptElement = document.getElementById('bag')

    const onCloseClicked = (e) => {
        if (!e.target.closest('.modal-content')) {
            onClose()
        }
    }

    return (
        ReactDOM.createPortal(
            <Container onClick={onCloseClicked}>
                <Content padding={padding} className="modal-content" width={width} fullHeight={fullHeight}>
                    {children}
                </Content>
            </Container>,
            promptElement
        )

    )
}

export default Modal




