import { createEvent, createStore } from 'effector'

const addPrompt = createEvent()
const deletePrompt = createEvent()

const initialPrompts = []

const $promptStore = createStore(initialPrompts)
.on(deletePrompt, (state, id) => state.filter(i => i.id !== id))
.on(addPrompt, (state, data) => {
    setTimeout(deletePrompt, 15000, data.id)
    return [...state, data]
})

export {
    $promptStore,
    addPrompt,
    deletePrompt
}


