import Faq from 'components/faq'
import React from 'react'

import {
    Title
} from '../styles'

import {
    FaqContent
} from 'styles'

const RecordFaq = () => {

    return (
        <Faq>
            <FaqContent>
                <Title margin="0px 0px 20px 0px">Записи</Title>
                Сохранение переписок. <br /> Включив сохранение переписки, Вы сможете всегда видеть историю ваших бесед, включая сообщений, которые были отредактированы или удалены.
            </FaqContent>
        </Faq>
    )
}

export {
    RecordFaq
}

