import React from 'react'
import { Input, Checkbox } from 'components/input'
import NotFound from 'components/not-found'
import { RecordFaq } from '.'
import {
    Title,
} from '../styles'
import {
    Wrapper,
    Flex
} from 'styles'

const RecordPage = () => {

    return (
        <>
            <Flex justify='space-between'>
                <Title>Записи</Title>
                <RecordFaq />
            </Flex>
                <Wrapper
                    margin="12px 0px"
                    padding="0px"
                    align="center"
                    justify="space-between"
                    shadow="none"
                >
                <Input
                    padding="0px 10px 0px 0px"
                    id="do_not_repeat"
                    type="search"
                    className="main"
                    placeholder="Поиск пользователя (имя/ID)"
                />
                <Checkbox
                    id="do_not_repeats"
                    className="main"
                    label="Сохранять переписки"
                />
                <NotFound title="Записей не найдено" caption="Сохраненных чатов пока нет" />
            </Wrapper>
        </>
    )
}

export {
    RecordPage
}
