import styled from 'styled-components'

const Container = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 102;
    transition: .3s;
    animation: fade-in .3s forwards ease-in-out;
    overflow-y: hidden;
`

const Content = styled.div`
    max-width: 90%;
    max-height: ${props => props.fullHeight ? 'calc(100vh - var(--spacing)* 2)' : '80vh'};
    min-height: 200px;
    width: ${props => props.width ? props.width : 'auto'};
    overflow-y: auto;
    padding: ${props => props.padding ? props.padding : 'var(--spacing)'};
    background-color: var(--gray-bcolor);
    border-radius: var(--bradius);
    @media (max-width: 500px) {
        width: 100%;
    }
`

export {
    Container,
    Content
}
