import React, { Suspense } from 'react'
import Dialog from './dialog'

import {
    Wrapper
} from 'styles'

const Chats = React.lazy(() => import('./chats'))

const ChatList = () => {

    return (
        <Wrapper width="100%" shadow="none" padding="0px" wrap="nowrap" justify="flex-start" margin="var(--spacing) 0px">
            <Suspense fallback={""}>
                <Chats />
                <Dialog />
            </Suspense>
        </Wrapper>
    )
}

export {
    ChatList
}

