import React from 'react'
import {
  BrowserRouter as Router,
  Switch,
  NavLink,
  Route
} from 'react-router-dom'
import Main from 'pages/main'
import Prompt from 'components/prompt'
import { Auth } from './pages/auth'
import { ChatList } from './pages/chat'


const App = () => {

  return (
    <Router>
      <Prompt />
      <div className="wrapper">
        <Switch>
          <Route path="/chat">
            <ChatList />
          </Route>
          <Route path="/auth">
            <Auth />
          </Route>
          <Route path="/" exact>
            <div style={{color: "#fff", textAlign: "center"}}>
              <h1 style={{textAlign: 'center', color: '#fff', margin: '50px', fontSize: '50px'}}>Welcome to TLine</h1>

              <NavLink className="link-button main" to="/settings">Get Started</NavLink>
            </div>
          </Route>
          <Main />
        </Switch>
      </div>
    </Router>

  )

}

export default App



