import React from 'react'
import {
    Switch,
    Route
} from 'react-router-dom'
import Settings from 'pages/settings'
import { ChatList } from 'pages/chat'


const Main = () => {

    return (
        <>
            <Switch>
                <Route path="/settings">
                    <Settings />
                </Route>
                <Route path="/user">
                    User
                </Route>
                <Route path="/chat">
                    <ChatList />
                </Route>
            </Switch>
        </>
    )
}

export default Main