import React, { useState } from 'react'
import { Input, Textarea } from 'components/input'
import { addPrompt } from 'utils/prompt'
import Button from 'components/button'
import { spinerOn } from 'utils/spinner'
import {
    Title
} from '../styles'
import {
    FormItem,
    Flex
} from 'styles'

const ShortcodeNew = ({ onClose, id, onAdd }) => {
    const [shortcode, setShortcode] = useState('')
    const [message, setMessage] = useState('')

    const onSubmit = (e) => {
        e.preventDefault()
        if (!message.trim() || !shortcode.trim()) {
            spinerOn()
            addPrompt(
                {
                    id: 'prompt_' + Math.random(),
                    message: "Одно или несколько полей не заполнено!"
                }
            )
            return false
        }
        onClose(false)
        onAdd(
            {
                id: `shortcode_${id+1}`,
                shortcode: shortcode.trim(),
                message: message.trim()
            }
        )
    }

    return (
        <FormItem onSubmit={onSubmit} className="new">
            <Title margin="0px auto 15px auto">Новый шорткод</Title>
            <Input
                padding="10px 0px"
                label="Шорткод"
                name="shortcode"
                placeholder="%hello_message"
                margin="10px 0px"
                className="main"
                autoComplete="off"
                value={shortcode}
                onChange={(e) => setShortcode(e.target.value)}
            />
            <Textarea
                padding="10px 0px"
                label="Сообщение"
                name="message"
                placeholder="Добрый день"
                margin="10px 0px"
                className="main"
                width="auto"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
            />
            <Flex justify="space-between" margin="10px 0px 0px">
                <Button className="gray" kind="small" type='button' onClick={() => onClose(false)}>
                    Отмена
                </Button>
                <Button className="main" kind="small">
                    Сохранить
                </Button>
            </Flex>
        </FormItem>
    )
}

export {
    ShortcodeNew
}
